@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap");
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 18px;
  background-color: #F5F5F5;
}

html,
body {
  overflow-x: hidden;
}

.blue {
  color: #1773FC;
}

.verification {
  margin: 140px 0;
}
.verification__title {
  font-size: 42px;
  line-height: 140%;
  margin-bottom: 80px;
  max-width: 589px;
}
.verification__container {
  max-width: 996px;
  width: 100%;
  margin: 0 auto;
}
.verification__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.verification__item {
  max-width: 387px;
  margin-bottom: 84px;
}
.verification__item-title {
  font-size: 26px;
  line-height: 140%;
  margin-bottom: 21px;
}
.verification__item-subtitle {
  line-height: 140%;
  font-size: 16px;
  color: #777E90;
}
.verification__button {
  max-width: 386px;
  width: 100%;
  padding: 5px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #1773FC;
  border-radius: 100px;
  border: none;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
}
.verification__button span {
  flex-grow: 1;
}

.testimonials__container {
  max-width: 1200px;
  padding: 0 10px;
  width: 100%;
  margin: 0 auto;
}
.testimonials__title {
  font-size: 42px;
  line-height: 140%;
  margin-bottom: 80px;
}
.testimonials__wrapper {
  display: flex;
  justify-content: space-between;
}
.testimonials__item {
  max-width: 590px;
  background-color: #fff;
  padding: 34px 40px;
  border-radius: 30px;
  box-shadow: 0px 16px 51px rgba(175, 189, 213, 0.14);
  display: flex;
  flex-direction: column;
  height: 100%;
}
.testimonials__item-support {
  padding-right: 20px;
  height: initial !important;
}
.testimonials__item-info {
  display: flex;
}
.testimonials__item-title {
  font-size: 26px;
  line-height: 140%;
}
.testimonials__item-advantage {
  color: #1773FC;
}
.testimonials__item-advantage svg {
  margin-right: 10px;
}
.testimonials__item-callback {
  color: #777E90;
  line-height: 140%;
}

.swiper {
  padding-bottom: 45px;
}

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background-color: #1773FC;
}

.swiper-button-next,
.swiper-button-prev {
  top: initial;
  bottom: 0;
}
.swiper-button-next::after,
.swiper-button-prev::after {
  content: none;
}
.swiper-button-next svg,
.swiper-button-prev svg {
  pointer-events: none;
}

@media (max-width: 1560px) {
  .step__container {
    padding: 0;
    padding-left: 15px;
    padding-right: 50px;
    margin: initial;
  }
}
@media (max-width: 1384px) {
  .step-arrow1 {
    max-width: 15vw;
  }
}
@media (max-width: 1366px) {
  .services__item {
    padding: 0 24px 57px 24px;
  }
  .services__item-list-item {
    font-size: 16px;
  }
  .services__item-list-support li {
    font-size: 16px;
  }

  .works .swiper-button-next,
.works .swiper-button-prev {
    min-width: 64px;
    min-height: 64px;
  }
  .works .swiper {
    padding-bottom: 100px;
  }
  .works__pagination {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  .works__pagination .swiper-button-next {
    margin-right: auto;
  }
  .works__pagination .swiper-button-prev {
    margin-left: auto;
  }
  .works__pagination .swiper-pagination {
    padding: 0 20px;
    width: initial !important;
    position: static;
  }
  .works .swiper-pagination-bullet:last-child {
    margin-right: 0 !important;
  }
}
@media (max-width: 1269px) {
  .step-arrow2, .step-arrow4 {
    display: none;
  }
  .step-arrow3 {
    display: flex;
    margin-bottom: 135px;
  }
  .step-arrow1, .step-arrow3 {
    max-width: 22vw;
  }
  .step-arrow1 {
    display: none;
  }
  .step .step-arrow1-1004 {
    display: flex;
    margin-top: 15%;
  }
  .step__list {
    justify-content: space-between;
  }
  .step__item {
    max-width: 35% !important;
    width: 100%;
    margin: 0 10px;
  }
  .step__item1 .step__item-title,
.step__item1 .step__item-subtitle {
    max-width: 284px;
  }
  .step__item2 .step__item-title,
.step__item2 .step__item-subtitle {
    max-width: 265px;
  }
  .step__item3 .step__item-title,
.step__item3 .step__item-subtitle {
    max-width: 272px;
  }
  .step__item4 .step__item-title,
.step__item4 .step__item-subtitle {
    max-width: 348px;
  }
  .step__item5 .step__item-title,
.step__item5 .step__item-subtitle {
    max-width: 280px;
  }
}
@media (max-width: 1200px) {
  .head__info {
    max-width: initial;
  }

  .client-pain__certificate {
    flex-direction: column;
  }
  .client-pain__certificate-container:last-child {
    margin-top: 50px;
    margin-left: 0;
    align-items: flex-start;
    flex-direction: row;
  }
  .client-pain__certificate-image {
    margin-top: 0;
    margin-left: 15px;
  }

  .step-arrow2 {
    display: none;
  }

  .works__item-button {
    margin-top: 38px;
  }
}
@media (max-width: 1024px) {
  .head {
    min-height: 100vh;
  }
  .head__container {
    flex-direction: column;
  }
  .head__info {
    margin-bottom: 10vh;
  }
  .head__title {
    margin: 17px 0 27px 0;
    max-width: 660px;
  }
  .head__image {
    margin-bottom: -65px;
  }

  .about-crm__container {
    flex-direction: column;
  }
  .about-crm__title {
    text-align: center;
    max-width: 600px;
    margin: 0 auto;
    margin-bottom: 39px;
  }
  .about-crm__info, .about-crm__subtitle {
    max-width: initial;
  }
  .about-crm__info {
    max-width: 90%;
  }
  .about-crm__image {
    margin-top: 91px;
  }

  .step__item {
    margin: 0 30px;
  }
  .step-arrow {
    display: none !important;
  }

  .services__title {
    margin-bottom: 36px;
  }
  .services__item {
    padding: 0 17px 14px 17px;
    margin-top: 36px;
  }
  .services__item-image {
    display: flex;
    align-items: center;
    max-width: 80%;
    margin: 0 auto;
    margin-top: -56px;
  }
  .services__item-body {
    height: 180px;
    overflow: hidden;
  }
  .services__item:nth-child(3) .services__item-more {
    opacity: 1;
  }
  .services__item-list-item {
    font-size: 13px;
  }
  .services__item-more {
    flex: 1 1 auto;
  }
  .services__item-price {
    font-size: 25px;
  }
  .services__item-list-support-title {
    font-size: 16px;
  }
  .services__item-list-support li {
    font-size: 13px;
  }
  .works__item {
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  .works__item-image {
    max-height: 443px;
    background-position: center;
  }
  .works__item-button {
    margin-top: 38px;
  }
  .works__item-title {
    margin-bottom: 35px;
  }
  .callback__container {
    flex-direction: column;
  }
  .callback__title {
    width: 100%;
    margin-bottom: 35px;
    text-align: center;
  }
  .callback__info {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .callback__subtitle {
    max-width: 325px;
  }
  .callback__map {
    margin-top: 57px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
}
@media (max-width: 768px) {
  .client-pain__list {
    flex-direction: column;
  }
  .client-pain__item {
    width: initial;
  }
  .client-pain__item:last-child {
    min-height: initial;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .step__item1 {
    max-width: 248px;
    min-width: 248px;
  }
  .step__item2 {
    max-width: 193px;
    min-width: 193px;
  }
  .step__item3 {
    max-width: 203px;
    min-width: 203px;
  }
  .step__item4 {
    max-width: 257px;
    min-width: 257px;
  }
  .step__item5 {
    max-width: 231px;
    min-width: 231px;
  }
  .step__list {
    margin-top: 46px;
  }

  .services__item-body {
    height: 190px;
  }
  .services__item-title {
    font-size: 20px;
  }
  .services__item-button {
    padding: 22px;
  }
  .services__item-list-support {
    margin-bottom: 15px;
  }
}
@media (max-width: 767px) {
  .client-pain__item:nth-child(2) {
    background-position: center;
  }
  .client-pain__certificate-image {
    margin-left: 0;
  }

  .services__list {
    flex-direction: column;
    align-items: center;
  }
  .services__item {
    margin-bottom: 40px;
  }
  .services__item:last-child {
    margin-bottom: 0;
  }
  .services__item-button {
    padding: 22px;
  }
}
@media (max-width: 640px) {
  body {
    font-size: 16px;
  }

  .head {
    padding-top: 0;
    min-height: initial;
  }
  .head__info {
    margin-bottom: 6vh;
  }
  .head__image {
    align-items: flex-start;
    margin-bottom: 20px;
    margin-right: -20px;
  }
  .head__image img {
    max-width: 472px;
  }

  .about-crm__info {
    max-width: initial;
  }
  .about-crm__quote p {
    display: inline;
  }
  .about-crm__wrapper-action {
    align-items: flex-start;
  }
  .about-crm__wrapper-action span {
    max-width: 210px;
  }

  .client-pain__item {
    min-height: 487px;
    padding: 36px 12px;
  }
  .client-pain__item:nth-child(5) {
    min-height: 487px;
    justify-content: flex-start;
    background-image: url("../img/client-pain-5-640px.png");
    background-position: right center;
  }
  .client-pain__item:nth-child(5) .client-pain__item-title {
    margin-bottom: 34px;
  }
  .client-pain__item:nth-child(6) {
    min-height: initial;
    margin: 0;
  }
  .client-pain__item:nth-child(6) .client-pain__item-title {
    font-size: 16px;
  }
  .client-pain__item::before {
    background: linear-gradient(90deg, rgba(255, 255, 255, 0.6) 20.21%, rgba(255, 255, 255, 0.76) 73.39%, rgba(255, 255, 255, 0.24) 93.91%);
    top: 0;
    left: 0;
    z-index: 2;
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
  }
  .client-pain__item-title {
    font-size: 18px;
  }
  .client-pain__item-support-subtitle {
    font-size: 15px;
  }
  .client-pain__item-list-item {
    font-size: 14px;
  }
  .client-pain__certificate {
    margin-top: 36px;
  }
  .client-pain__certificate-container {
    flex-direction: initial;
  }
  .client-pain__certificate-container:last-child {
    flex-direction: column;
  }
  .client-pain__certificate-title {
    font-size: 23px;
  }
  .client-pain__certificate-bitrix img {
    max-width: 200px;
  }
  .client-pain__certificate-subtitle {
    font-size: 18px;
  }
  .client-pain__certificate-image {
    margin-top: 23px;
    margin-left: 0;
  }
  .client-pain__certificate-container:last-child {
    min-width: initial;
  }

  .step__list-title, .step__list-subtitle, .step__item-title, .step__item-subtitle {
    max-width: initial !important;
  }
  .step__container {
    padding: 0 10px;
  }
  .step__list {
    display: flex;
    flex-direction: column;
  }
  .step__item {
    color: #fff;
    min-width: initial;
    max-width: 100% !important;
    display: flex;
    align-items: flex-start;
    margin-left: 0;
    margin-bottom: 40px;
  }
  .step__item-suptitle, .step__item-title, .step__item-subtitle {
    font-family: "Helvetica", sans-serif;
    color: inherit;
  }
  .step__item-suptitle {
    color: rgba(255, 255, 255, 0.4);
    font-size: 40px;
    margin-right: 4px;
  }
  .step__item-suptitle::before {
    width: 20px;
    height: 20px;
    bottom: 1px;
  }
  .step__item-title {
    font-size: 22px;
    display: flex;
    justify-content: space-between;
  }
  .step__item-control {
    min-width: 45px;
    min-height: 45px;
    margin-left: 10px;
  }
  .step__item-control svg {
    transform: rotate(180deg);
  }
  .works__pagination {
    justify-content: center;
  }
  .works__pagination .swiper-button-next,
.works__pagination .swiper-button-prev {
    display: none;
  }
  .works .swiper {
    padding-bottom: 35px;
  }
  .works__item-container {
    padding: 15px;
  }
  .works__item-image {
    max-height: 253px;
  }
  .works__item-tasks {
    margin-bottom: 20px;
  }
  .works__item-tasks, .works__item-result {
    flex-direction: column;
  }
  .works__item-tasks-title, .works__item-result-title {
    margin-bottom: 13px;
  }
  .works__item-button {
    margin-left: 0;
    margin-bottom: 46px;
  }

  .callback__info {
    min-width: initial;
  }
  .callback__phone span, .callback__mail span {
    font-size: 26px;
  }
  .callback__title {
    text-align: left;
  }
  .callback__form {
    display: none;
  }
  .callback__mail {
    margin-bottom: 0px;
  }
}
@media (max-width: 414px) {
  .works__item-button {
    max-width: 100%;
  }
}